<script>
export default {
    data(){
        return{
            dp_period:['Class Teacher','1','2','3','4','5','6','7'],
            recep_data:[],
            headers: [
                { text: 'Action',value :'action',sortable: false},
                { text: 'Name',value:'name',sortable: false},
            ], 
            key:'',
            hv_record:false,
            curr_username:'',
            curr_userid:'',
            hv_user:false,
            ready_change:false,
            by_day:['1','2','3','4','5','6'],
            day_list:['','','','','',''],
            order:'day',
            blk:false,
            ava_lesson_list:[],
            ava_session_list:[],
            sel_sup:false,
            tmp_date:'',
            message:[],
            swap_loc:[],
            swap_sub:[],
            swap_date:[],
            swap_day:[],
            swap_session:[],
            old_session:'',
            original_date:'',
            cycle_date:[],
            swap_subject_code:'',
            loc_list:[],
            sup1:'',
            sup2:'',
            sup3:'',
            app_type:'swap',
            selected_cls:'',
            selected_loc:'',
            subject_list:[],
            subject_code:'',
            type:'official',
            user_check:'',
            ava_list:[],
            select_day:'',
            select_session:'',
            target_date:'',
            open_list:false,
            tdy_date:'',
            style:[],
            tdy_day:'',
            lesson:[],   //format : lesson[session][day]
            subject:[],
            location:[],
            class_a:[],
            ready:false,
        }
    },
    watch: {
      tdy_date: function (newDate, oldDate) {
         this.original_date = oldDate;
       },
       tmp_date: function (newDate, oldDate) {
         this.original_date = oldDate;
       }
    },   
    async created(){
        var today = new Date();
        var dd = String(today.getDate()).padStart(2, '0');
        var mm = String(today.getMonth() + 1).padStart(2, '0'); //January is 0!
        var yyyy = today.getFullYear();
            today = yyyy + '-' + mm + '-' + dd;
        this.tdy_date = today;
        await this.check_sub_date();
        var i = 0;
        var j = 0;
        for(i=0;i<8;i++){
            this.message[i] = [];
            this.style[i] = [];
            this.lesson[i] = [];
            this.subject[i] = [];
            this.location[i] = [];
            this.class_a[i] = [];
            for(j=0;j<8;j++){
                this.message[i][j] = '';
                this.style[i][j] = 'pointer-events:none';
                this.lesson[i][j] = '';
                this.subject[i][j] = '';
                this.location[i][j] = '';
                this.class_a[i][j]='';
            }
        }
        await this.fetch_cycle();
        await this.fetch_subject();
        await this.fetch_loc();
        /*
        await this.fetch_timetable();
        await this.check_overlay();
        */
        this.ready_change = true;  
        this.ready = true;
    },
    async mounted(){
 
    },
    methods:{
        async search_user(){
            var formData = new FormData();
            formData.append('key',this.key);
            await axios.post('subsitude/sub/backend/search_user.php',formData)
                .then(
                    (response)=>{
                        if(response.data!='empty'){
                            this.recep_data = response.data;
                            this.hv_record = true;
                        } else {
                            this.hv_record = false;
                        }
                    }
                )
        },
        async select_user(username,name){
            this.curr_username = name;
            this.curr_userid = username;
            this.key = name;
            this.hv_record = false;
            await this.fetch_timetable();
        },
        async change_view(){           
                this.ready_change = false;
                var j = 0;
                var m = 0;
                for(j=0;j<6;j++){
                    this.cycle_date[j]='';
                    this.by_day[j]='';
                    for(m=0;m<8;m++){
                        this.lesson[j][m] = '';
                        this.subject[j][m] = '';
                        this.class_a[j][m] = '';
                        this.location[j][m] = '';
                        this.message[j][m] = '';
                        this.style[j][m] = 'pointer-events:none';
                    }
                }
                if(this.order=='date'){
                var formData = new FormData();
                formData.append('date',this.tdy_date);
                formData.append('sel_user',this.curr_userid);
                await axios.post('subsitude/sub/backend/change_view_sec.php',formData)
                    .then(
                        (response)=>{
                            this.day_list = response.data;
                            var i = 0;
                            var s = 0;
                            for(i=0;i<this.day_list.length;i++){
                                this.cycle_date[i] = this.day_list[i].date;
                                this.by_day[i] = this.day_list[i].day;
                                s=0;
                                for(s=0;s<8;s++){
                                    if(this.tdy_date == this.cycle_date[i]){
                                        this.style[i][s]="cursor:pointer;background-color:rgba(178, 255, 186, 0.75)";
                                    }
                                    this.lesson[i][s] = this.day_list[i][s].subject;
                                    this.subject[i][s] = this.day_list[i][s].subject;
                                    this.class_a[i][s] = this.day_list[i][s].class;
                                    this.location[i][s] = this.day_list[i][s].classroom;
                                }
                            }
                        }
                    )
                    await this.new_day();
                    await this.check_overlay();
                    this.ready_change = true;
            } else {
                /*
                await this.fetch_cycle();
                await this.fetch_subject();
                await this.fetch_loc();
                await this.fetch_timetable();                
                await this.check_sub_date();
                await this.check_overlay();*/
                this.new_day();
                this.ready_change = true;
            }
        },
        change_lesson(){
            var i = 0;
            var tmp_sess = 0;
            var session = Number(this.swap_session[0]);
            for(i=0;i<this.ava_session_list.length;i++){
                tmp_sess = Number(this.ava_lesson_list[i].session);
                if(tmp_sess==session){
                    this.swap_loc[0] = this.ava_lesson_list[i].location;
                    this.swap_sub[0] = this.ava_lesson_list[i].subject;
                }
            }           
            this.$forceUpdate();
        },
        async fetch_info(mode){   //auto choose session + location + subject by class+date+day+username
            var formData = new FormData();
            formData.append('apply_session',this.select_session);
            formData.append('teacher',this.sup1);
            formData.append('original_day',this.tdy_day);
            formData.append('original_loc',this.selected_loc);
            formData.append('mmode',mode);
            formData.append('date',this.swap_date[0]);
            formData.append('class',this.selected_cls);
            formData.append('curr_teacher',this.curr_userid);
            if(mode==1){
                this.sup1 = 'Please Select Date first';
            }
            await axios.post('subsitude/sub/backend/swap_info.php',formData)
                .then(
                    (response)=>{
                        if(mode==1){
                            this.ava_session_list = [];
                            this.ava_list = response.data;
                            if(this.ava_list[0].name!=''){
                                this.sel_sup = true;
                            } else {
                                this.sup1 = 'No available Teacher';
                                this.sel_sup = false;
                                this.$alert('No available swap pair');
                            }
                        } else if(mode==0){
                            if(response.data[0].session!=''){
                                this.ava_session_list = [];
                                this.ava_lesson_list = response.data;
                                this.swap_session[0] = response.data[0].session;
                                this.swap_loc[0] = response.data[0].location;
                                this.swap_sub[0] = response.data[0].subject;
                                var i = 0;
                                for(i=0;i<response.data.length;i++){
                                    this.ava_session_list[i] = response.data[i];
                                }
                            } else {
                                this.$alert('Swap Record Exist. Teacher in not available');
                            }
                        }                                           
                        this.$forceUpdate();
                    }
                )
        },
        async check_overlay(){
            var i = 0;            
            for(i=0;i<6;i++){
                var formData = new FormData();
                formData.append('sel_user',this.curr_userid);
                formData.append('cycle_date',this.cycle_date[i]);
                await axios.post('subsitude/sub/backend/fetch_leave_sec.php',formData)
                    .then(
                        (response)=>{
                            if(response.data!='empty'){
                                var n=0;
                                var k=0; //day
                                var s=0; //session
                                var l=0;
                                for(l=0;l<response.data.length;l++){
                                    if(this.order=='day'){
                                        k = response.data[l].day;
                                        s = response.data[l].session;
                                    } else if(this.order=='date'){
                                        for(n=0;n<6;n++){
                                            if(response.data[l].date == this.cycle_date[n]){
                                                k = n+1;
                                            }
                                        }
                                        s = response.data[l].session;
                                    }
                                    this.subject[k-1][s] = response.data[l].subject;
                                    this.class_a[k-1][s] = response.data[l].class;
                                    this.location[k-1][s] = response.data[l].location;
                                    this.message[k-1][s] = 'Leave type : '+response.data[l].type+' '+response.data[l].app_type+' by '+response.data[l].assigned_id;
                                    if(response.data[l].type=='sick'){
                                        this.style[k-1][s] = 'background-color:rgb(170, 128, 255);color:rgba(255,255,255,0.8);pointer-events:none';
                                    } else {
                                        this.style[k-1][s] = 'background-color:rgb(170, 128, 255);color:rgba(255,255,255,0.8);pointer-events:none';
                                    }
                                }
                            }
                        }
                    )
            }
        },
        async fetch_cycle(){
            await axios.get('subsitude/sub/backend/fetch_cycle.php')
            .then(
                (response)=>{
                    if(response.data!='empty'){
                        this.tdy_day = response.data[0].day;
                        var tmp_day = Number(this.tdy_day)-1;
                        var i = 0;
                        var clr ='';
                        for(i=0;i<8;i++){                     
                            if(i%2 == 0){
                                clr = 'rgba(145, 255, 156, 0.75)';
                            } else {
                                clr = 'rgba(178, 255, 186, 0.75)';
                            }
                            this.style[tmp_day][i] = 'cursor:pointer;background-color:'+ clr;
                        }
                    }
                }
            )
        },
        async fetch_subject(){
            await axios.get('subsitude/sub/backend/fetch_subject.php')
            .then(
                (response)=>{
                    this.subject_list = response.data;
                    this.subject_code = this.subject_list[0].subject_code;
                }
            )
        },
        async fetch_loc(){
            await axios.get('subsitude/sub/backend/fetch_loc.php')
            .then(
                (response)=>{
                    this.loc_list = response.data;
                }
            )
        },
        async fetch_timetable(){
            var formData = new FormData();
            formData.append('sel_user',this.curr_userid);
            await axios.post('subsitude/sub/backend/fetch_time_table_sec.php',formData)
            .then(
                (response)=>{
                    var i = 0;
                    var j = 0;
                    var counter = 0;
                    for(i=0;i<6;i++){
                        for(j=0;j<8;j++){
                            this.lesson[i][j] = response.data[counter].subject;
                            if(this.lesson[i][j]==''){
                                this.check_sub_record(i,j);
                            } else {
                                this.class_a[i][j] = response.data[counter].class;
                                this.subject[i][j] = response.data[counter].subject;
                                this.location[i][j] = response.data[counter].classroom;
                            }                            
                            counter++;
                        }
                    }
                    this.hv_user = true;
                }
            )  
        },
        async fetch_day(i){
            var date = this.swap_date[i];
            var old_date = this.tmp_date;
            var formData = new FormData();
            formData.append('date',this.swap_date[i]);
            await axios.post('subsitude/sub/backend/new_day.php',formData)
                .then(
                    (response)=>{
                        if(response.data=='empty'){
                            this.$alert('Selected Date is not a cycle Day !');
                            this.swap_date[i] = old_date;
                            this.$forceUpdate();
                        } else {
                            this.swap_day[i] = response.data[0].day;
                            this.tmp_date = this.swap_date[i];
                            this.$forceUpdate();
                        }
                    }
                )
        },
        async new_day(){
            var tmp = '';
            if(this.order=='day'){                
                var formData = new FormData();
                formData.append('date',this.tdy_date);
                await axios.post('subsitude/sub/backend/new_day.php',formData)
                    .then(
                        (response)=>{
                            if(!(response.data=='empty')){
                                tmp = '';
                                this.tdy_day = response.data[0].day;
                                var tmp_day = Number(this.tdy_day)-1;
                                var i = 0;
                                var clr ='';
                                var n = 0;
                                var m = 0;
                                for(n=0;n<6;n++){
                                    for(m=0;m<8;m++){
                                        this.style[n][m] = 'pointer-events:none';
                                    }
                                }
                                for(i=0;i<8;i++){                     
                                    if(i%2 == 0){
                                        clr = 'rgba(145, 255, 156, 0.75)';
                                    } else {
                                        clr = 'rgba(178, 255, 186, 0.75)';
                                    }
                                    this.style[tmp_day][i] = 'cursor:pointer;background-color:'+ clr;
                                }                                                      
                            } else {
                                tmp = 'empty'
                                this.$alert('Selected Date is not a cycle Day !');
                                this.tdy_date = this.original_date;
                                /*
                                var k=0;
                                var l=0;
                                for(k=0;k<8;k++){
                                    for(l=0;l<8;l++){
                                        this.style[k][l] = 'pointer-events:none';
                                    }
                                    
                                }*/
                            }
                        }
                    )
                    if(tmp!='empty'){
                        await this.check_sub_date();
                        await this.reload_timetable();
                        await this.check_overlay();  
                    }
                    this.$forceUpdate();
            } else {    //order date
                this.ready_change = false;
                var j = 0;
                var m = 0;
                for(j=0;j<6;j++){
                    this.cycle_date[j]='';
                    this.by_day[j]='';
                    for(m=0;m<8;m++){
                        this.lesson[j][m] = '';
                        this.subject[j][m] = '';
                        this.class_a[j][m] = '';
                        this.location[j][m] = '';
                        this.message[j][m] = '';
                        this.style[j][m] = 'pointer-events:none';
                    }
                }
                var formData = new FormData();
                formData.append('date',this.tdy_date);
                formData.append('sel_user',this.curr_userid);
                await axios.post('subsitude/sub/backend/change_view_sec.php',formData)
                    .then(
                        (response)=>{
                            if(!(response.data=='empty')){
                                tmp = '';
                                this.day_list = response.data;
                                var i = 0;
                                var s = 0;
                                for(i=0;i<this.day_list.length;i++){
                                    this.cycle_date[i] = this.day_list[i].date;
                                    this.by_day[i] = this.day_list[i].day;
                                    s=0;
                                    for(s=0;s<8;s++){
                                        if(this.tdy_date == this.cycle_date[i]){
                                            this.style[i][s]="cursor:pointer;background-color:rgba(178, 255, 186, 0.75)";
                                        }
                                        this.lesson[i][s] = this.day_list[i][s].subject;
                                        this.subject[i][s] = this.day_list[i][s].subject;
                                        this.class_a[i][s] = this.day_list[i][s].class;
                                        this.location[i][s] = this.day_list[i][s].classroom;
                                    }
                                }
                            } else {
                                tmp = 'empty'
                                this.$alert('Selected Date is not a cycle Day !');
                                this.tdy_date = this.original_date;
                            }
                        }
                    )
                    if(tmp!='empty'){
                        await this.check_sub_date();
                        await this.reload_timetable();
                        await this.check_overlay();  
                    }
                    this.$forceUpdate();
                    this.ready_change = true;
            }
        },
        async reload_timetable(){
            if(this.order=='day'){
                var formData = new FormData();
                formData.append('sel_user',this.curr_userid);
                await axios.post('subsitude/sub/backend/fetch_time_table_sec.php',formData)   //day
                .then(
                    (response)=>{
                        var i = 0;
                        var j = 0;
                        var counter = 0;
                        for(i=0;i<6;i++){
                            for(j=0;j<8;j++){
                                this.lesson[i][j] = response.data[counter].subject;
                                if(this.lesson[i][j]==''){
                                    this.check_sub_record(i,j);
                                } else {
                                    this.message[i][j]='';
                                    this.class_a[i][j] = response.data[counter].class;
                                    this.subject[i][j] = response.data[counter].subject;
                                    this.location[i][j] = response.data[counter].classroom;
                                }                            
                                counter++;
                            }
                        }
                    }
                )
            } else {
                var i = 0;
                var j = 0;
                var d = 0;
                var s = 0;
                for(i=0;i<6;i++){
                    var formData = new FormData();
                    formData.append('date',this.cycle_date[i]);
                    await axios.post('subsitude/sub/backend/sub_by_date.php',formData)
                        .then(
                            (response)=>{
                                if(response.data!=''){
                                    for(j=0;j<response.data.length;j++){
                                        d = Number(response.data[j].day)-1;
                                        s = response.data[j].session;
                                        this.class_a[d][s] = response.data[j].class;
                                        this.subject[d][s] = response.data[j].subject;
                                        this.location[d][s] = response.data[j].location;
                                        this.message[d][s] = response.data[j].app_type + ' - ( From : ' + response.data[j].leave_id + ')';
                                        this.style[d][s] = 'background-color:rgb(138, 155, 242);color:rgba(60, 60, 60, 0.7);pointer-events:none';
                                    }
                                }
                            }
                        )
                }
            }
        },
        close_overlay(){
            this.open_list = false;
        },
        leave_type(){
            this.old_session = this.select_session;
            if(this.type=='sick'){     
                /*         
                if(this.select_session!='Full Day'){
                    this.old_session = this.select_session;
                    this.select_session = 'Full Day';
                }*/
                this.app_type ='sup';
            } else {
                this.select_session = this.old_session;
                this.app_type = 'swap';
            }
        },
        async check_sub_date(){
            var formData = new FormData();
            formData.append('date',this.tdy_date);
            formData.append('sel_user',this.curr_userid);
            await axios.post('subsitude/sub/backend/fetch_cycle_date.php',formData)
                .then(
                    (response)=>{
                        var i = 0;
                        for(i=0;i<response.data.length;i++){
                            if(!(response.data == 'No cycle day')){
                                if(!(response.data[i]==undefined)||!(response.data[i]==null)){
                                    var index = response.data[i].day;
                                    index = Number(index)-1;
                                    this.cycle_date[index] = response.data[i].date;
                                }
                            }
                        }
                    }
                )
        },
        async check_sub_record(i,j){
            var formData = new FormData();
            formData.append('cycle_date',this.cycle_date[i]);
            formData.append('day',(i+1));
            formData.append('session',j);
            await axios.post('subsitude/sub/backend/check_sub_info.php',formData)
                .then(
                    (response)=>{
                        if(response.data.length>0){
                            this.message[i][j] =response.data[0].app_type + ' - ( From : ' + response.data[0].leave_id + ')';
                            this.lesson[i][j] = response.data[0].subject;
                            this.subject[i][j] = response.data[0].subject;
                            this.class_a[i][j] = response.data[0].class;
                            this.location[i][j] = response.data[0].location;
                            this.style[i][j] = 'background-color:rgb(138, 155, 242);color:rgba(60, 60, 60, 0.7);pointer-events:none';
                        } else {
                            this.message[i][j] = '';
                            this.lesson[i][j] = '';
                            this.subject[i][j] = '';
                            this.class_a[i][j] = '';
                            this.location[i][j] = '';
                        }
                    }
                )
            this.$forceUpdate();
        },
        async swap(i,j){
            this.blk = false;
            var l = 0;
            for(l=0;l<3;l++){
                this.swap_sub[l] = 'Select Subject';
                this.swap_loc[l] = 'Select Location';
                this.swap_date[l] = 'Select Date';
                this.swap_day[l] = 'Select Day';
                this.swap_session[l] = 'Select Session';
            }
            this.type = 'official';
            this.app_type = 'swap';
            var day = this.by_day[i];
            this.select_day = this.by_day[i];
            var session = j;
            this.select_session = session;
            var formData = new FormData();
            formData.append('sel_date',this.tdy_date);
            formData.append('session',session);
            formData.append('day',day);
            formData.append('app_type',this.app_type);
            formData.append('class',this.class_a[i][j]);
            await axios.post('subsitude/sub/backend/fetch_ava_teacher.php',formData)
                .then(
                    (response)=>{
                        this.ava_list = response.data;
                        this.subject_code = this.subject[i][j];
                        this.selected_cls = this.class_a[i][j];
                        this.selected_loc = this.location[i][j];
                        this.sup1 = 'Please Select Date first';
                        this.sup2 = '';
                        this.sup3 = '';
                    }
                )
            if(this.swap_sub[0].indexOf("Blk")!=-1){
                this.blk = true;
                this.app_type = 'sup';
            }
            this.open_list=true;
            this.tmp_date = this.tdy_date;
        },
        async new_list(){
            var i = Number(this.select_day)-1;
            var j = this.select_session;
            var formData = new FormData();
            formData.append('sel_date',this.tdy_date);
            formData.append('session',this.select_session);
            formData.append('day',this.select_day);
            formData.append('app_type',this.app_type);
            formData.append('class',this.class_a[i][j]);
            await axios.post('subsitude/sub/backend/fetch_ava_teacher.php',formData)
                .then(
                    (response)=>{
                        this.ava_list = response.data;
                    }
                )
            this.$forceUpdate();
        },
        handleSubmit(){
            var formData = new FormData();
            formData.append('sel_user',this.curr_userid);
            formData.append('sel_date',this.tdy_date);
            formData.append('day',this.select_day);
            formData.append('session',this.select_session);
            formData.append('original_subject',this.subject_code);
            formData.append('swap_subject',this.swap_subject_code);
            formData.append('class',this.selected_cls);
            formData.append('location',this.selected_loc);
            formData.append('type',this.type);
            formData.append('app_type',this.app_type);
            formData.append('sup1',this.sup1);
            formData.append('sup2',this.sup2);
            formData.append('sup3',this.sup3);
            //append swap info if is swap
            if((this.app_type=='swap')||(this.app_type=='swap_rec')){
                var i = 0;
                for(i=0;i<3;i++){
                    formData.append('subject'+i,this.swap_sub[i]);
                    formData.append('location'+i,this.swap_loc[i]);
                    formData.append('date'+i,this.swap_date[i]);
                    formData.append('day'+i,this.swap_day[i]);
                    formData.append('session'+i,this.swap_session[i]);
                }
            }
            //
            axios.post('subsitude/sub/backend/apply_swap_sec.php',formData)
                .then(
                    (response)=>{
                        if(response.data=="Successfully Submitted Application"){
                            this.$alert(response.data);
                            this.open_list = false;
                            this.$router.push('../admin/sup_op');
                        } else {
                            this.$alert(response.data);
                        }
                    }
                )
        }
    }
}
</script>
<style scoped>
th{
    width:15.7%;
    text-align: center;
    font-size: 1.1rem;
}
td{
    padding-left:1.25rem;
    padding-right:1.25rem;
    border:2px solid #ffffff;
    height:5rem;   
    transition: all 0.3s ease-in-out;
}
td:hover{
    background-color:rgba(160, 237, 255,0.8)!important;
}
.inner_overlay{
    z-index: 5;
    width:65%;
    max-height:60%;
    position:absolute;
    margin-left:auto;
    margin-top:auto;
    margin-bottom:auto;
    margin-right:auto;
    top:0;
    bottom:0;
    left:0;
    right:0;
    background-color:rgba(255, 255, 255, 0.95);
    box-shadow: 5px 5px 5px 5px rgba(211, 211, 211, 0.6);
}
.fa-window-close{
    transition: all 0.15s ease-in-out;
    color:rgba(255, 255, 255, 0.55);
}
.fa-window-close:hover{
    color:rgba(255, 255, 255, 1);
}
.sub_tr{
    height:5rem!important;
}
</style>



